export default {
	props: {
		score: {
			type: Number
		}
	},
	data() {
		return {
			scoreWidth: 0,
			fullScore: 5, //满分
		}
	},
	created() {

		// 获取星级总长度
		const query = uni.createSelectorQuery().in(this).select('.noscore'); // 这里要加上select
		query.boundingClientRect(data => {
			this.scoreWidth = Number(this.score * data.width) / this.fullScore
		}).exec();












	}
}
